<template>
  <v-container>
    <v-row class="text-center bg-primary-gradient">
      <v-col cols="4" style="position: relative">
        <v-avatar size="120">
          <v-img :src="user.photo" contain/>
        </v-avatar>
        <div style="position: absolute; bottom: 20px; left: calc(50% + 40px);">
          <v-btn icon :to="{name:'ProfileIndex'}">
            <v-icon>mdi-camera</v-icon>
          </v-btn>
        </div>
      </v-col>

      <v-col cols="8" align-self="center">
        <h1 class="display-1 font-weight-bold profile-title">{{ user.fullName }}</h1>
      </v-col>
    </v-row>
    <v-row style="background-color: #EDEDED">
      <v-col cols="12"></v-col>
    </v-row>
    <v-row class="text-center">
      <v-col v-for="(item, i) in items" :key="i" cols="4">
        <template v-if="item.visible">
          <div @click="goTo(item.link)">
            <div style="background-color: #EDEDED; border-radius: 1rem; padding: 0.5rem">
              <v-img
                  :src="require('../assets/icons/' + item.icon)"
                  class="my-3"
                  contain
                  height="48"
              />
            </div>
            <div class="mt-2">{{ item.title }}</div>
          </div>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: 'HelloWorld',
  data: () => ({
    items: [
      {title: 'Mi abogado LegalHelp', icon: 'home-01.png', link: '/my-lawyer', visible: true},
      {title: 'Abogado de emergencia', icon: 'home-02.svg', link: '/lawyer/emergency', visible: true},
      {title: 'Documentos en linea', icon: 'home-03.png', link: '/documents/online', visible: true},
      {title: 'Ubica tu institución', icon: 'home-04.svg', link: '/map', visible: true},
      {title: 'Requisitos legales', icon: 'home-05.svg', link: '/requirements', visible: true},
      {title: 'Enlaces de interés', icon: 'home-06.svg', link: '/links', visible: true},
      {title: 'Estado de tus tramites', icon: 'home-07.svg', link: '/operations', visible: true},
      {title: 'Estado de tus consultas', icon: 'home-08.svg', link: '/requests', visible: true},
      {title: 'Calculadora LEGALHELP', icon: 'home-09.svg', link: '/calculator', visible: true},
      {title: 'Manual de usuario', icon: 'home-10.svg', link: '/manual', visible: true},
      {title: '', icon: '', link: '/', visible: false},
      {title: 'Soporte Técnico', icon: 'home-11.svg', link: '/support', visible: true},
    ]
  }),
  computed: {
    ...mapGetters(['user'])
  },
  methods: {
    goTo(url) {
      this.$router.push(url)
    }
  }
}
</script>
