import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

//axios.defaults.baseURL = 'http://18.219.225.148/api'; // aws server
//axios.defaults.baseURL = 'http://legalhelp.local/api'; // local server
axios.defaults.baseURL = process.env.VUE_APP_API_BASE_URL;
axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('api_token')

const CONSULTATION_STATUS_PENDING_PAYMENT = 1 // pendiente de pago
const CONSULTATION_STATUS_IN_ATTENTION = 4 // en atencion
const CONSULTATION_STATUS_FINISHED = 6 // terminado

export default new Vuex.Store({
    state: {
        token: localStorage.getItem('api_token') || null,
        user: JSON.parse(localStorage.getItem('user')) || null,
        services: [],
        service: null,
        operation: {
            selected: JSON.parse(localStorage.getItem('operation')) || null,
            formData: JSON.parse(localStorage.getItem('operation_form_data')) || [],
            coupon: JSON.parse(localStorage.getItem('operation_coupon')) || null,
        },
    },
    getters: {
        apiToken(state) {
            return state.token
        },
        loggedIn(state) {
            let hasToken = state.token !== null
            let hasUser = state.user instanceof Object

            return hasToken && hasUser
        },
        user(state) {
            return state.user
        },
        services(state) {
            return state.services
        },
        service(state) {
            return state.service
        },
        operation(state) {
            return state.operation.selected
        },
        operationFormData(state) {
            return state.operation.formData
        },
        getConsultationInAttentionId() {
            return CONSULTATION_STATUS_IN_ATTENTION
        },
        getConsultationFinishedId() {
            return CONSULTATION_STATUS_FINISHED
        },
        getConsultationPendingPaymentId() {
            return CONSULTATION_STATUS_PENDING_PAYMENT
        },
    },
    mutations: {
        saveToken(state, payload) {
            state.token = payload
            localStorage.setItem('api_token', payload)
        },
        saveUser(state, payload) {
            state.user = payload
            localStorage.setItem('user', JSON.stringify(payload))
        },
        logout(state) {
            state.token = null
            state.user = null
            localStorage.removeItem('api_token')
            localStorage.removeItem('user')
        },
        saveServices(state, payload) {
            state.services = payload
        },
        saveService(state, payload) {
            state.service = payload
            state.service.price_final = payload.price
        },
        updateService(state, {field, value}) {
            state.service[field] = value
        },
        saveOperation(state, payload) {
            state.operation.selected = payload
            localStorage.setItem('operation', JSON.stringify(payload))
        },
        saveOperationFormData(state, payload) {
            state.operation.formData = payload
            localStorage.setItem('operation_form_data', JSON.stringify(payload))
        },
        updateOperation(state, {field, value}) {
            state.operation.selected[field] = value
            localStorage.setItem('operation', JSON.stringify(state.operation.selected))
        },
    },
    actions: {
        loadData(context) {
            return axios.get('/services').then((response) => {
                context.commit('saveServices', response.data.data)
            })
        },
        login(context, payload) {
            context.commit('saveToken', payload.api_token)
            context.commit('saveUser', payload)
            window.location.href = '/'
        },
        logout(context) {
            context.commit('logout')
        },
        loadUser(context) {
            return axios.get('/profile').then((response) => {
                context.commit('saveToken', response.data.api_token)
                context.commit('saveUser', response.data)
            })
        },
        selectService(context, payload) {
            context.commit('saveService', payload)
        },
        addCodeToService(context, code) {
            context.commit('updateService', {field: 'coupon_code', value: code})
        },
        addPriceToService(context, price) {
            context.commit('updateService', {field: 'price_final', value: price})
        },
        selectOperation(context, payload) {
            context.commit('saveOperation', payload)
            context.commit('updateOperation', {field: 'price_final', value: payload.price})
        },
        selectOperationFormData(context, payload) {
            context.commit('saveOperationFormData', payload)
        },
        addCodeToOperation(context, code) {
            context.commit('updateOperation', {field: 'coupon_code', value: code})
        },
        addPriceToOperation(context, price) {
            context.commit('updateOperation', {field: 'price_final', value: price})
        },
    },
    modules: {}
})
