<template>
  <v-app>
    <v-app-bar
        v-if="loggedIn"
        app
        color="primary"
        dark
    >
      <div class="d-flex align-center">
        <v-icon @click="goHome">mdi-home</v-icon>
      </div>

      <v-spacer></v-spacer>

      <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn v-bind="attrs" v-on="on" dark icon>
            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item link :to="{name: 'ProfileIndex'}">
            <v-list-item-title>
              <v-icon>mdi-account</v-icon>
              Perfil de usuario
            </v-list-item-title>
          </v-list-item>
          <v-list-item link :to="{name: 'ChangePassword'}">
            <v-list-item-title>
              <v-icon>mdi-lock</v-icon>
              Cambiar Contraseña
            </v-list-item-title>
          </v-list-item>
          <v-list-item link @click="logout">
            <v-list-item-title>
              <v-icon>mdi-door-open</v-icon>
              Salir
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <v-main>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
//import {mapActions} from 'vuex'
import {mapGetters} from 'vuex'

export default {
  name: 'App',

  data: () => ({
    //
  }),
  computed: {
    ...mapGetters(['loggedIn', 'user'])
  },
  methods: {
    goHome() {
      this.$router.push({name: 'Home'})
    },
    logout() {
      this.$store.dispatch('logout')
      this.$router.push({name: 'Start'})
    }
  }
};
</script>

