import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
    {path: '/', name: 'Home', component: Home},
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {path: '/start', name: 'Start', component: () => import('../views/Start.vue')},
    {path: '/login', name: 'Login', component: () => import('../views/Login.vue')},
    {path: '/register', name: 'Register', component: () => import('../views/Register.vue')},
    {path: '/password', name: 'Password', component: () => import('../views/Password.vue')},

    /**
     * My Lawyer
     */
    {path: '/my-lawyer', name: 'MyLawyer', component: () => import('../views/MyLawyer/Index')},
    {path: '/my-lawyer/:id', name: 'MyLawyerPaymentType', component: () => import('../views/MyLawyer/Service')},
    {path: '/my-lawyer/:id/payment', name: 'MyLawyerPayment', component: () => import('../views/MyLawyer/Payment')},
    {path: '/my-lawyer/:id/bank', name: 'MyLawyerBank', component: () => import('../views/MyLawyer/Bank')},
    {
        path: '/my-lawyer/:id/payment/confirm/:operationId',
        name: 'MyLawyerPaymentConfirm',
        component: () => import('../views/MyLawyer/PaymentConfirm.vue')
    },
    {
        path: '/my-lawyer/start/:type',
        name: 'MyLawyerPaymentStart',
        component: () => import('../views/MyLawyer/PaymentStart.vue')
    },
    {
        path: '/my-lawyer/chat',
        name: 'MyLawyerPaymentChat',
        component: () => import('../views/MyLawyer/PaymentChat.vue')
    },
    {path: '/lawyer/emergency', name: 'Emergency', component: () => import('../views/Emergency/Chat.vue')},
    /**
     * Documents Simple
     */
    {path: '/documents/online', name: 'DocumentsIndex', component: () => import('../views/Documents/Index.vue')},
    {path: '/documents/level1/:id', name: 'DocumentsLevel1', component: () => import('../views/Documents/Level1.vue')},
    {
        path: '/documents/level2/:id/:id2',
        name: 'DocumentsLevel2',
        component: () => import('../views/Documents/Level2.vue')
    },
    {
        path: '/documents/detail/:id/:id2/:id3',
        name: 'DocumentsDetail',
        component: () => import('../views/Documents/Detail.vue')
    },
    {
        path: '/documents/variable/:id/detail',
        name: 'DocumentsDetailVariable',
        component: () => import('../views/Documents/Variable/Detail')
    },
    {path: '/documents/payment', name: 'DocumentsPayment', component: () => import('../views/Documents/Payment')},
    {
        path: '/documents/payment-method/operation/:id',
        name: 'DocumentsPaymentMethod',
        component: () => import('../views/Documents/PaymentMethod')
    },
    {path: '/documents/bank', name: 'DocumentsBank', component: () => import('../views/Documents/Bank')},
    {
        path: '/documents/payment/confirm/:id',
        name: 'DocumentsPaymentConfirm',
        component: () => import('../views/Documents/PaymentConfirm.vue')
    },
    {path: '/documents/my-data/:id', name: 'DocumentsData', component: () => import('../views/Documents/Data.vue')},
    {
        path: '/documents/my-data/confirm/:id',
        name: 'DocumentsDataConfirm',
        component: () => import('../views/Documents/DataConfirm.vue')
    },
    {
        path: '/documents/my-data/validate/:id',
        name: 'DocumentsDataValidate',
        component: () => import('../views/Documents/DataValidate')
    },
    {
        path: '/documents/my-data/:id/complete',
        name: 'DocumentsDataComplete',
        component: () => import('../views/Documents/DataComplete.vue')
    },

    /**
     * Documents Variable
     */
    {
        path: '/documents/:id',
        name: 'DocumentVariableIndex',
        component: () => import('../views/Documents/Variable/Index')
    },
    {
        path: '/documents/operation/:id/data',
        name: 'DocumentVariableDataForm',
        component: () => import('../views/Documents/Variable/DataForm')
    },
    {
        path: '/documents/operation/:id/data/confirm',
        name: 'DocumentVariableDataConfirm',
        component: () => import('../views/Documents/Variable/DataConfirm')
    },
    {
        path: '/documents/operation/:id/data/confirmed',
        name: 'DocumentVariableDataConfirmed',
        component: () => import('../views/Documents/Variable/DataConfirmed')
    },
    {
        path: '/documents/operation/:id/payment/methods',
        name: 'DocumentVariablePaymentMethod',
        component: () => import('../views/Documents/Variable/PaymentMethod')
    },
    {
        path: '/documents/operation/:id/payment/online',
        name: 'DocumentVariablePaymentOnline',
        component: () => import('../views/Documents/Variable/PaymentOnline')
    },
    {
        path: '/documents/operation/:id/payment/voucher',
        name: 'DocumentVariablePaymentVoucher',
        component: () => import('../views/Documents/Variable/PaymentVoucher')
    },
    {
        path: '/documents/operation/:id/payment/online/confirmed',
        name: 'DocumentVariablePaymentConfirmed',
        component: () => import('../views/Documents/Variable/PaymentConfirmed')
    },
    /**
     * Requirements
     */
    {path: '/requirements', name: 'RequirementsIndex', component: () => import('../views/Requirements/Index')},
    {
        path: '/requirements/level1/:id',
        name: 'RequirementsLevel1',
        component: () => import('../views/Requirements/Level1.vue')
    },
    {
        path: '/requirements/level2/:id/:id2',
        name: 'RequirementsLevel2',
        component: () => import('../views/Requirements/Level2.vue')
    },
    {
        path: '/requirements/detail/:id/:id2/:id3',
        name: 'RequirementsDetail',
        component: () => import('../views/Requirements/Detail.vue')
    },
    {path: '/requirements/chat', name: 'RequirementsChat', component: () => import('../views/Requirements/Chat')},
    // links
    {path: '/links', name: 'LinksIndex', component: () => import('../views/Links/Index')},
    // calculator
    {path: '/calculator', name: 'CalculatorIndex', component: () => import('../views/Calculator/Index')},
    {path: '/calculator/01', name: 'Calculator01', component: () => import('../views/Calculator/Detail01')},
    {path: '/calculator/02', name: 'Calculator02', component: () => import('../views/Calculator/Detail02')},
    {path: '/calculator/03', name: 'Calculator03', component: () => import('../views/Calculator/Detail03')},
    // manual
    {path: '/manual', name: 'ManualIndex', component: () => import('../views/Manual/Index')},
    // support
    {path: '/support', name: 'SupportChat', component: () => import('../views/Support/Chat')},
    /**
     * Operations
     */
    {path: '/operations', name: 'OperationIndex', component: () => import('../views/Operations/Index')},
    {path: '/operations/:id', name: 'OperationDetail', component: () => import('../views/Operations/Detail')},
    {path: '/operations/:id/payment', name: 'OperationPayment', component: () => import('../views/Operations/Payment')},
    {path: '/operations/:id/invoice', name: 'OperationInvoice', component: () => import('../views/Operations/Invoice')},
    {path: '/operations/:id/chat', name: 'OperationChat', component: () => import('../views/Operations/Chat')},
    {
        path: '/operations/:id/pending-confirm',
        name: 'OperationsPendingConfirm',
        component: () => import('../views/Operations/PendingConfirm')
    },
    // request
    {path: '/requests', name: 'RequestIndex', component: () => import('../views/Requests/Index')},
    {path: '/requests/:id', name: 'RequestDetail', component: () => import('../views/Requests/Detail')},
    {path: '/requests/:id/start', name: 'RequestStart', component: () => import('../views/Requests/Start')},
    {path: '/requests/:id/payment', name: 'RequestPayment', component: () => import('../views/Requests/Payment')},
    {path: '/requests/:id/invoice', name: 'RequestInvoice', component: () => import('../views/Requests/Invoice')},
    {
        path: '/requests/:id/pending-confirm',
        name: 'RequestPendingConfirm',
        component: () => import('../views/Requests/PendingConfirm')
    },
    // map
    {path: '/map', name: 'MapIndex', component: () => import('../views/Map/Index')},
    /**
     * Profile
     */
    {path: '/profile', name: 'ProfileIndex', component: () => import('../views/Profile/Index')},
    {path: '/change-password', name: 'ChangePassword', component: () => import('../views/Profile/ChangePassword')},
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach(async (to, from, next) => {
    let apiToken = store.getters['apiToken'];
    let isAuthenticated = store.getters['loggedIn'];

    if (apiToken !== null) {
        await store.dispatch('loadUser')
    }

    const publicPages = ['Start', 'Login', 'Register', 'Password'];
    const authRequired = !publicPages.includes(to.name);
    //const loggedIn = localStorage.getItem('user');

    if (authRequired && !isAuthenticated) {
        next({name: 'Start'});
    } else {
        await store.dispatch('loadData')
        next();
    }

    //if (to.name !== 'Start' && !isAuthenticated) next({name: 'Start'})
    //else next()
})

export default router